@import "./reset";
//font

//color
$white: #ffffff;
$gray-400: #dee2e6;
$gray-700: #707070;
$black: #000000;
$black-op2: #00000029;
$red-800: #650300;
$red-500: #aa0601;
$red-100: #fbf2f2;
//1920 - 312ï¼Š2 = 1296
.container {
  max-width: 1296px;
  //margin: 0 auto;
}
.header {
  height: 80px;
  background-color: $red-500;
  color: $white;
  display: flex;
  justify-content: space-between;
  .logo {
    width: 80px;
    height: 49px;
    img{
      width: 100%;
    }
  }
  .nav{
    font-size: 20px;
  }
}

.footer {
  height: 200px;
  background-color: #00cc99;
}

.box {
  width: 100px;
  height: 100px;
  background-color: red;
  display: flex;
  transition: all 0.5s;
  &:hover {
    width: 200px;
    background-color: $red-100;
  }
}
